//  request 
import request from './respberry.js'

// 
export const capture = (time) => {
	return request({
		url: `/capture`,
		method: 'get',
	})
}
// 
export const handleOX = (option) => {
	return request({
		url: `/handleOX?action=${option}`,
		method: 'get',
	})
}

export const handleLED = (option) => {
	return request({
		url: `/handleLED?action=${option}`,
		method: 'get',
	})
}