//  request 
import request from './index.js'

// pH
export const getPAC = (time) => {
	return request({
		url: `/system/getPhAndConductivityList?currentTime=${time}`,
		method: 'get',
	})
}
// 
export const getNPK = (time) => {
	return request({
		url: `/system/soilElements?currentTime=${time}`,
		method: 'get',
	})
}