// 
const base_url = 'http://124.221.52.73:80'

// token
export default (params) => {
	let url = params.url;
	let method = params.method || "get";
	let data = params.data || {};
	return new Promise((resolve, reject) => {
		uni.request({
			url: base_url + url,
			method: method,
			// header: header,
			sslVerify: false,
			data: data,
			success(response) {
				const res = response
				// 
				//
				// console.log(res.statusCode);
				if (res.statusCode == 200) {
					resolve(res.data);
				} else {
					uni.clearStorageSync()
					switch (res.statusCode) {
						case 401:
							uni.showModal({
								title: "",
								content: "",
								showCancel: false,
								success() {
									setTimeout(() => {
										uni.navigateTo({
											url: "/pages/login/index",
										})
									}, 1000);
								},
							});
							break;
						case 404:
							uni.showToast({
								title: '...',
								duration: 2000,
							})
							break;
						default:
							uni.showToast({
								title: '...',
								duration: 2000,
							})
							break;
					}
				}
			},
			fail(err) {
				console.log(err)
				if (err.errMsg.indexOf('request:fail') !== -1) {
					uni.showToast({
						title: '',
						icon: "error",
						duration: 2000
					})
				} else {
					uni.showToast({
						title: '',
						duration: 2000
					})
				}
				reject(err);

			},
			complete() {
				// 
				uni.hideLoading();
				uni.hideToast();
			}
		});
	}).catch(() => {});
};
